import { Component, OnInit, HostListener, ViewChild, ElementRef } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';

import { ScrollToService, ScrollToConfigOptions } from '@nicky-lenaers/ngx-scroll-to';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  isNavExpanded:boolean = false;
  isMobile:boolean = false;
  activeLink:any = "";

  contactForm: FormGroup;



  @ViewChild('nav',{static: false}) allNavs: ElementRef;

  constructor(
    private _scrollToService: ScrollToService,
    private fb: FormBuilder
    ) { }

  toggleNav(){
  	this.isNavExpanded = !this.isNavExpanded;
  }

  gotoContentSection(id){

    const config: ScrollToConfigOptions = {
      target: id,
      offset:-90
    };

    this.activeLink = id;
    this.isNavExpanded = !this.isNavExpanded;
    this._scrollToService.scrollTo(config);

  }

  isActiveLink(id){
    return id == this.activeLink ? 'active-link' : '';
  }

  @HostListener('window:scroll', ['$event'])
  onWindowScroll($event) {

    if(!this.isMobile) {

      var navHeight = document.querySelector('.navbar-collapse').clientHeight;
      var navItems = this.allNavs.nativeElement.querySelectorAll('.nav-link');

      var fromTop = window.scrollY + navHeight + 70;
      var scrollItems = [];
      var curElem = [];

      navItems.forEach(elem => {
       var target = elem.id.split('-');
       scrollItems.push(target[0]);
      });

      scrollItems.forEach((elem,index) => {
          
          if(elem != ""){
            if (document.getElementById(elem).offsetTop < fromTop){
              return curElem.push(elem);
            }
          }

      });

      curElem = curElem[curElem.length-1];
      if(this.activeLink != "" || this.activeLink != "contact")
      this.activeLink = curElem;

    }

    


  }

  submitContactForm(){

    if (this.contactForm.invalid) {
        
     Object.keys(this.contactForm.controls).forEach((key: string) => {

       const abstractControl = this.contactForm.get(key);
       if(abstractControl.value == ""){
         abstractControl.markAsDirty();
       }

     });

    }

  }

  ngOnInit() {

    if(window.innerWidth <= 991)
      this.isMobile = true;
    else
      this.isMobile = false;

     window.onresize = () => {
      this.isMobile = window.innerWidth <= 991;
    }

    this.contactForm = this.fb.group({
      fullName: ['', Validators.required],
      email: ['', [Validators.required, Validators.email]],
      message: ['', Validators.required]
    })


  }

}
